import React from "react";

export default function Header() {
    return (
      <nav className="nav">
        <div className="site-title">
          <img src="../assets/logo.svg" alt="logo" />
        </div>
        <ul>
          <li>
          <button>Book Show</button>
          </li>
          <li>
          <button>Login</button>
          </li>
        </ul>
      </nav>
    )
  }


